import React from 'react';
import ReactDOM from 'react-dom';
import LoginButton from 'widgets/login-button/LoginButtonWidget';

const ATTRIBUTES = {
  sessionUser: 'session-user',
  userDisplayName: 'user-display-name',
  currentPage: 'page',
  applicationBaseURL: 'app-url',
};

class LoginButtonElement extends HTMLElement {
  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  attributeChangedCallback(attribute, oldValue, newValue) {
    if (!Object.values(ATTRIBUTES).includes(attribute)) {
      throw new Error(`Untracked changed attribute: ${attribute}`);
    }
    if (this.mountPoint && newValue !== oldValue) {
      this.render();
    }
  }

  connectedCallback() {
    this.mountPoint = document.createElement('div');
    this.appendChild(this.mountPoint);
    this.render();
  }

  render() {
    const sessionUser = this.getAttribute(ATTRIBUTES.sessionUser);
    const userDisplayName = this.getAttribute(ATTRIBUTES.userDisplayName);
    const currentPage = this.getAttribute(ATTRIBUTES.currentPage);
    const applicationBaseURL = this.getAttribute(ATTRIBUTES.applicationBaseURL);

    ReactDOM.render(
      <LoginButton
        sessionUser={sessionUser}
        userDisplayName={userDisplayName}
        currentPage={currentPage}
        applicationBaseURL={applicationBaseURL}
      />,
      this.mountPoint
    );
  }
}

if (!customElements.get('login-button-widget')) {
  customElements.define('login-button-widget', LoginButtonElement);
}

export default LoginButtonElement;
