import React from 'react';
import PropTypes from 'prop-types';
import { useKeycloak } from 'react-keycloak';
import { HeaderNavigation, HeaderMenuItem } from 'carbon-components-react';
import HeaderMenu from 'common/ui-shell/HeaderMenu';
import { ChevronDown16 } from '@carbon/icons-react';
import { Button } from 'carbon-components-react';
import { settings } from 'carbon-components';

import './LoginButtonWidget.scss';

const { prefix } = settings;

const ButtonKeycloakLogic = ({ applicationBaseURL, sessionUser, userDisplayName, currentPage }) => {
  const [keycloak] = useKeycloak();

  const handleLogin = () => {
    window.location.href = `${applicationBaseURL}do/login?redirectTo=${currentPage}`;
  };

  return (
    <div className="LoginButtonWidget">
      {keycloak.authenticated ? (
        <div className="LoginButtonWidget__wellcome">
          <HeaderNavigation aria-label="My Account">
            <HeaderMenu
              aria-label={userDisplayName}
              menuLinkName={userDisplayName}
              renderMenuContent={() => <ChevronDown16 className={`${prefix}--header__menu-arrow`} />}
              className="navigationMenu"
            >
              <HeaderMenuItem href={`${applicationBaseURL}do/main`}>
                Administration
              </HeaderMenuItem>
              <HeaderMenuItem href={`${applicationBaseURL}do/logout.action`}>
                Logout
              </HeaderMenuItem>
            </HeaderMenu>
          </HeaderNavigation>
        </div>
      ) : (
        <Button onClick={handleLogin}>Login</Button>
      )}
    </div>
  )

}

ButtonKeycloakLogic.propTypes = {
  applicationBaseURL: PropTypes.string.isRequired,
  sessionUser: PropTypes.string,
  userDisplayName: PropTypes.string,
  currentPage: PropTypes.string,
};

ButtonKeycloakLogic.defaultProps = {
  sessionUser: '',
  userDisplayName: '',
  currentPage: '',
};


export default ButtonKeycloakLogic;
