import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Keycloak from 'keycloak-js';
import { KeycloakProvider } from 'react-keycloak';
import ButtonKeycloakLogic from './ButtonKeycloakLogic';

class LoginButtonWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keycloak: null,
    };
  }

  componentDidMount() {
    const { applicationBaseURL } = this.props;    
    const keycloak = new Keycloak(`${applicationBaseURL}keycloak.json`);
    this.setState({ keycloak });
  }

  render() {
    const { keycloak } = this.state;
    const config = { onLoad: 'check-sso', checkLoginIframe: true };
    if (!keycloak) {
      return null;
    }
    return (
      <KeycloakProvider keycloak={keycloak} initConfig={config}>
        <ButtonKeycloakLogic {...this.props} />
      </KeycloakProvider>
    );
  }
}

LoginButtonWidget.propTypes = {
  applicationBaseURL: PropTypes.string,
};

LoginButtonWidget.defaultProps = {
  applicationBaseURL: process.env.REACT_APP_BASEURL,
};

export default LoginButtonWidget;
